
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'


export default {
components: {
XHeader,
Group,
XInput,
XButton,
PopupPicker,
XAddress
},
data() {
return {
options: {
type: [],
},
addressData: ChinaAddressV4Data,
}
},
methods: {
getEnterpriseType: function() {
axios.post('api/enterprise/getEnterpriseType').then(response => {
this.options.type = this.formatSelectOptionsForVux(response.data.data);
console.log(this.options.type);
});
}
},
mounted() {
this.getEnterpriseType();
}
}



